import logo from './logo.svg';
import './App.css';
import { render } from 'react-dom';
import React, {Component} from 'react';
import Acks from './components/acks'

class App extends Component{
  state = {
    acks: []
  }

  componentDidMount() {
    fetch('/ack')
    .then(res => res.json())
    .then((data) => {
      if(data == null){
        data =[]
      }
      this.setState({acks: data})
    })
    .catch(console.log)
  }
  
  render() {
    return (
      <div>
      <h1>ACKs, TXs, More</h1>
      <Acks acks={this.state.particles} />
      </div>
    );
  }
}

export default App;
